import React from 'react';
import { Row, Col, Button, Typography, Tag } from 'antd';
const { Title, Text } = Typography;

const TAG_COLOR = 'blue';
class Profile extends React.Component {
    renderLinks = (links) => {
        const elements = links.map((item, idx) => {
            return <Button key={idx} className='profile-button' onClick={e => window.open(item.url, '_blank')} type="link" icon={item.icon}></Button>
        })

        return elements;
    }

    renderResouce = (data) => {
        const elements = data.map((item, idx) => {
            return <Button key={idx} onClick={e => window.open(item.url, '_blank')} type="link"><Tag color='#108ee9'>{item.title}</Tag></Button>
        })

        return elements;
    }

    renderTags = (data) => {
        const elements = data.map((item, idx) => {
            return <Tag key={idx} color={TAG_COLOR}>{item.title}</Tag>
        })

        return elements;
    }

    render() {
        const { profile }  = this.props;
        const { name, picture, position, employer, links, summary, languages, resource } = profile;
        return (
            <div>
                <img className='profile-picture' src={picture} alt="App Icon" width="100" height="100" />
                <p className='profile-title'>{name}</p>
                {this.renderResouce(resource)}
                <Title level={4}>{summary}</Title>
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <Text className='profile-employer' type="secondary">{position}</Text>
                    <Text className='profile-employer' type="secondary">{employer}</Text>                    
                </div>
                <br />
                {this.renderTags(languages)}
                <br />
                <br />
                <br />
                <br />
                <div style={{ display: 'flex', flexDirection: 'row' }}>
                    {this.renderLinks(links)}
                </div>
            </div>
        );
    }
}

export default Profile;
